<template>
	<div class="detail">
		<van-nav-bar class="nav-bar" title="新闻详情" left-text="返回" left-arrow @click-left="onClickLeft" />
		<div class="detail-item">
			<div class="title">{{this.result.title}}</div>
			<div class="info">
				<div>
					<span>{{this.result.author}}</span>
					<span style="margin-left:10px;">{{this.result.publishtime}}</span>
				</div>
				<div class="title-icon">
					<van-icon name="more-o" /><span style="margin-left: 5px;">0</span>
				</div>
			</div>
			<div class="content" v-html="this.result.content"></div>
		</div>
	</div>
</template>

<script>
	import * as api from '@/api/index.js'
	export default {
		data(){
			return{
				result:""
			}
		},
		activated(){
			// console.log(this.$route.query)
			let newsid = this.$route.query.newsid
			// console.log(newsid)
			this.getNews(newsid)
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			async getNews(id){
				let res = await api.getNews(id)
				this.result = res.Data
				// console.log(this.result)
			}
		}
	}
</script>

<style>
	.content img{
		width: 100%;
		height: 100%;
	}
	.detail .detail-item .info {
		font-size: 12px;
		color: #999;
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;

	}

	.detail .detail-item .title {
		font-size: 22px;
		line-height: 33px;
		color: #333;
		margin-bottom: 10px;
		font-weight: 700;

	}

	.van-nav-bar__title,
	.van-nav-bar .van-icon,
	.van-nav-bar__text {
		color: #FFFFFF !important;
	}

	.detail .nav-bar {
		background: #5A99F6;
	}

	.detail-item {
		padding: 15px;
	}
</style>
